<template>
  <div>
    <MovieList title="popular" :movie="$store.state.movie.popularMovie" />
    <MovieList title="Now-playing" :movie="$store.state.movie.nowPlayingMovie" />
    <MovieList title="Upcoming" :movie="$store.state.movie.upComingMovie" />
  </div>
</template>

<script>
import MovieList from '@/components/MovieList.vue';
export default {
  name: 'Movie',
  components: {
    MovieList,
  },
  mounted(){
    this.getInitMovie();
  },
  methods: {
     getInitMovie(){
      this.$store.dispatch('movie/getPopularMovie');
      this.$store.dispatch('movie/getNowPlayingMovie');
      this.$store.dispatch('movie/getUpcomingMovie');
    }
  }
}
</script>
