import axios from 'axios';
const request = axios.create({
    baseURL: "https://api.themoviedb.org/3",
    params:{
        api_key: 'b30bf4513060923037b45f2402075645',
        language: "ko-KR"
    },
});

export default{
    namespaced: true,
    state: {
        popualarMovie: [],
        nowPlayingMovie: [],
        upComingMovie: [],
        movieDetail: []
    },
    mutations: {
        SET_POPULAR_MOVIE(state, data){
            state.popularMovie = data;
        },
        SET_NOW_PLAYING_MOVIE(state, data){
            state.nowPlayingMovie = data;
        },
        SET_UPCOMING_MOVIE(state, data){
            state.upComingMovie = data;
        },
        SET_DETAIL_MOVIE(state, data){
            state.movieDetail = data;
        }
    },
    actions: {
        async getPopularMovie({commit}){

           const result = await request("/movie/popular",{
                params: {
                    page: '1',
                },
            });
            
              if(result.status === 200){
                console.log(result.data.results);
                commit('SET_POPULAR_MOVIE', result.data.results);
              }        
        },
        async getNowPlayingMovie({commit}){
            const result = await request("/movie/now_playing");
            // const result = await axios.get(
            //     'https://api.themoviedb.org/3/movie/now_playing?api_key=b30bf4513060923037b45f2402075645&language=ko-KR'
            //   );
              if(result.status === 200){
                //console.log(result.data.results);
                commit('SET_NOW_PLAYING_MOVIE', result.data.results);
              }        
        },
        async getUpcomingMovie({commit}){
            const result = await request("/movie/upcoming");
            // const result = await axios.get(
            //     'https://api.themoviedb.org/3/movie/upcoming?api_key=b30bf4513060923037b45f2402075645&language=ko-KR'
            //   );
              if(result.status === 200){
                //console.log(result.data.results);
                commit('SET_UPCOMING_MOVIE', result.data.results);
              }        
        },
        async getDetailMovie({commit}, movieId){
            const result = await request("/movie/"+ movieId, {
                params: {
                    append_to_response: 'videos',
                },
            });
            // const result = await axios.get(
            // `https://api.themoviedb.org/3/movie/${movieId}?api_key=b30bf4513060923037b45f2402075645&language=ko-KR`
            //   );
              console.log(result);
              if(result.status === 200){
                //console.log(result.data.results);
                commit('SET_DETAIL_MOVIE', result.data);
              }        
        },
    },
};